      text4:
        'SELECT * FROM DPARP.MLCARDUD\nWHERE MEMBER_CONT = 1 AND PROMOCODE = 1\nFOR FETCH ONLY\nWITH UR;'
    }
  },
  setup() {},
  created() {},
  mounted() {
    this.cdcrGrid1 = CustomUI.createGrid('cdcrGrid1', true, GridData.cdcQuery1Columns)
    this.cdcrGrid1.appendRows(GridData.cdcQuery1Data)

    this.cdcrGrid2 = CustomUI.createGridNMenu(
      'cdcrGrid2',
      true,
      GridData.cdcQuery2Columns,
      ({ rowKey, columnName }) => [
        [
          {
            name: 'open',
            label: '',
            action: () => {
              this.openQuery = true
              console.log(rowKey + '/' + columnName)
            }
          }
        ]
      ]
    )
    // this.cdcrGrid2.on('click', (ev) => {
    //   console.log('check!', ev.columnName)
    // })
    Grid.setLanguage('ko')
    this.cdcrGrid2.appendRows(GridData.cdcQuery2Data)
  },
  unmounted() {},
  methods: {
    closeDialog() {
      this.$emit('callParent')
    }
  },
  updated() {}
}
</script>
